.c {
    background-color: #282c34;
    color: #61dafb;
    height: 100vh;
    position: relative;
}

.c-bg{
    background-color: #61dafb;
    width: 20px;
    height: 100%;
    position: absolute;
}

.c-wrapper {
    padding: 50px;
    display: flex;
}

.c-left {
    flex: 1;
}

.c-right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-title {
    font-style: 60px;
    width: 80%;
    padding-bottom: 30px;
    margin-left: 15px;
    margin-top: 16%;
}

.c-info-item{
    display: flex;
    align-items: center;
}

.c-icon {
    width: 30px;
    height: 30px;
    padding: 15px;
}

.c-desc {
    margin-top: 16%;
}

form{
    margin-top: 3%;
}

input {
    margin: 10px 0px;
    background-color: #cfd7da;
    color:rgb(0, 12, 22);
    border-radius: 5px;
    width: 50%;
    height: 35px;
    border: none;
    border-bottom: 1px solid #61dafb;
    font-size: 14px;
    font-weight: bold;
}

textarea {
    margin: 10px 0px;
    width: 100%;
    background-color: #cfd7da;
    color: rgb(0, 12, 22);
    border-radius: 5px;
    font-size: 14px;
    border: none;
    border-bottom: 1px solid #61dafb;
    font-weight: bold;
}

button {
    border: none;
    padding: 15px;
    background-color: #61dafb;
    border-radius: 5px;
    color: rgb(0, 12, 22);
    font-weight: 600;
    cursor: pointer;
}

.thanks {
    margin-top: 15px;
}