.pl {
    height: 100vh;
    display: flex;
    background-color: #282c34;
    background-image: url('https://github.com/Will-Neal/react-portfolio-wwn/blob/main/src/img/st-annes-backround.jpg?raw=true');
    color: #61dafb;
    padding: 100px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
    
}

.pl-texts {
    width: 65%;
    z-index: 1;
}

.pl-title {
    font-size: 45px;
    font-weight: 600;
}

.pl-description {
    margin: 20px 0px;
}

.pl-list {
    height: 100%;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.pl-bg {
    position: absolute;
    clip-path: polygon(0 0, 48% 0, 100% 100%, 0% 100%);
    background-color:#282c34;
    height: 110%;
    width: 110%;
    z-index: 0;
    top: -4.3%;
}